import * as types from './mutation-types'

export default {
  [types.LOGIN_NOTYFY] (state, payload) {
  	state.loginNotify = payload
  },
  [types.CART_NUM] (state, payload) {
  	state.cartNum = payload
  },
  [types.LOGIN_STATUS] (state, payload) {
  	state.loginStatus = payload
  },
  [types.CART_POSITION] (state, payload) {
    state.cartPosition = payload
  },
  [types.EDIT_ADDRESS] (state,payload){//编辑地址展开状态
    state.isShowForm = payload
  },
  [types.UPDATE_NAME] (state, payload) {
    state.updateName = payload
  },
  [types.AVATAR] (state, payload) {
    state.avatar = payload
  },
}



