import Vue from 'vue'
import Vuex from 'vuex'
import actions from './actions'
import mutations from './mutations'

Vue.use(Vuex)

const state = {
  loginStatus: null, // 登录状态
  loginNotify: false, // 登录完成后通知其它组件更改状态
  cartNum: 0, // 购物车商品数量
  cartPosition: 0, // 购物车位置
  isShowForm:false, // 编辑地址状态
  updateName: false, // 更新昵称
  avatar: null, // 头像
}

export default new Vuex.Store({
  namespaced: true,
  state,
  actions,
  mutations
})



