<template>
  <div id="app" v-cloak>
    <Header />
     <keep-alive>
      <router-view v-if="$route.meta.keepAlive"></router-view>
    </keep-alive>

    <router-view v-if="!$route.meta.keepAlive">
      <!-- 这里是不被缓存的视图组件 -->
    </router-view>
    <Footer />
  </div>
</template>

<script>
import Vue from 'vue'
import Header from '@/components/Header.vue'
import Footer from '@/components/Footer.vue'

function getOffsetValue (node, prop) {
  let propValue = node[prop]
  let parent = node.offsetParent
  while(parent !== null) {
    propValue += node[prop]
    parent = parent.offsetParent
  }
  return propValue
}

export default {
  components: {
    Header,
    Footer
  },
  created () {
    this.$store.dispatch('getLoginStatus')
    Vue.prototype.getOffsetValue = getOffsetValue
  }
}
</script>

<style>
[v-cloak] {display:none}
</style>



