import * as types from './mutation-types'
import axios from 'axios'
import api from '../api/api'

export default {
  getLoginStatus ({ commit }, payload) {
    commit(types.LOGIN_STATUS, sessionStorage.getItem('token') ? true : false)
  },
  setLoginNotify ({ commit }, payload) {
    commit(types.LOGIN_NOTYFY, payload)
  },
  getCartNum ({ commit }, payload) {
   if(!this.state.loginStatus){
     commit(types.CART_NUM, 0);
     return false
   }
    axios.get(api.getCartNum).then(res => {
      if (res.status === 200 && res.data.code === 200) {
        commit(types.CART_NUM, res.data.result)
      } else {
        commit(types.CART_NUM, 0)
      }
    }).catch(err => {
      commit(types.CART_NUM, 0)
    })
  },
  setCartPosition ({ commit }, payload) {
    commit(types.CART_POSITION, payload)
  },
  setAddressTypeStatus ({ commit }, payload) {
    commit(types.EDIT_ADDRESS, payload)
  },
  setUpdateName ({ commit }, payload) {
    commit(types.UPDATE_NAME, payload)
  },
  setAvatar ({ commit }, payload) {
    commit(types.AVATAR, payload)
  }
}



