<!--底部-->
<template>
  <div id="footer">
    <div class="mark-wrapper">
      <div>
        <img src="@/static/images/1.png">
        <img src="@/static/images/2.png">
        <img src="@/static/images/3.png">
        <img src="@/static/images/4.png">
      </div>
    </div>
    <div class="guide-wrapper">
      <ul v-for="(classify, index) in list">
        <li class="classify-title">{{ classify.title }}</li>
        <li v-for="(item, index) in classify.links" class="link-wrapper">
          <a href="javascript:;" @click="openPage(classify,item,index)">{{ item.name }}</a>
        </li>
      </ul>
    </div>
    <div class="copyright">Copyright &copy; 2017-2030银犁食品  (ylfood.com)   All rights reserved.       蜀ICP备:11006129号-3    网站统计    站长统计</div>
  </div>
</template>
<script>
export default {
  name: 'Footer',
  methods:{
    openPage(classify,item,index){
      window.scrollTo(0,0)
      this.$router.push(`/helpCenter?p=${classify.value}&v=${item.value||index}`);
    }
  },
  data () {
    return {
      list: [{
        title: '新手指南',
        value:'help_shop',
        links: [{
          name: '用户注册',
          value: ''
        }, {
          name: '购物流程',
          value: ''
        },
//          {
//          name: '订购方式',
//          value: ''
//        },
          {
          name: '设置密码',
          value: ''
        }]
      }, {
        title: '付款方式',
        value:"pay_ways",
        links: [{
          name: '支付方式',
          value: ''
        }, {
          name: '充值说明/储值卡说明',
          value: ''
        }]
      }, {
        title: '配送方式',
        value:"send_ways",
        links: [{
          name: '配送运费',
          value: '0'
        }, {
          name: '配送范围',
          value: '0'
        }, {
          name: '配送时间',
          value: '0'
        }, {
          name: '站点列表',
          value: '0'
        }]
      }, {
        title: '售后服务',
        value:"afterSalesService",
        links: [{
          name: '换货政策',
          value: ''
        }, {
          name: '换货流程',
          value: '0'
        }, {
          name: '投诉与建议',
          value: '1'
        }]
      }, {
        title: '关于我们',
        value:"aboutUs",
        links: [{
          name: '公司介绍',
          value: ''
        }, {
          name: '联系我们',
          value: ''
        }, {
          name: '业务合作',
          value: ''
        }, {
          name: '微信公众号',
          value: ''
        }, {
          name: 'APP',
          value: ''
        }]
      }]
    }
  }
}
</script>
<style lang="stylus">
#footer
  margin-top: 50px
  .mark-wrapper
    background: #f6f1e2
    > div
      margin: 0 auto
      display: flex
      justify-content: space-between
      align-items: center
      width: 1000px
      height: 90px
  .guide-wrapper
    margin: 0 auto
    padding: 20px 0
    display: flex
    justify-content: space-between
    width: 800px
    .classify-title
      font-size: 14px
      font-weight: 700
    .link-wrapper
      margin-top: 5px
      a
        color: #666
        font-size: 12px
        &:hover
          color: #36C573
  .copyright
    height: 50px
    line-height: 50px
    color: #666
    font-size: 12px
    text-align: center
    white-space: pre-wrap
</style>



