<!--头部-->
<template>
<div>
  <div id="header" :class="!isCancel && scrollTop > 40 ? 'follow' : ''">
    <div class="header-wrapper" v-show="isCancel || scrollTop <= 40">
      <span style="float: left;">送至：四川成都市</span>
      <span style="float: right;">
        <a
          href="javascript:;"
          v-if="userInfo"
          @click="$router.push({ name: 'User', query: { nav: 'userInfo', subNav: 'basic' } })"
        >
          {{ userInfo.userName || userInfo.mobile }}
        </a>
        <a href="javascript:;" @click="$router.push({ name: 'Login' })" v-else>登录</a>
        <i class="separator"></i>
        <a href="javascript:;" @click="$router.push({ name: 'User', query: { nav: 'order', subNav: 'all' } })">我的订单</a>
        <i class="separator"></i>
        <a href="javascript:;" class="app-link">
          银犁APP
          <div class="app-wrapper">
            <img src="../static/images/Android.png">
            <span style="position: absolute; top: 112px; left: 35px;">Android</span>
            <img src="../static/images/iOS.png">
            <span style="position: absolute; top: 112px; right: 50px;">iOS</span>
          </div>
        </a>
        <i class="separator"></i>
        <span href="javascript:;" class="service-link">
          客服
          <div class="phone">400-1111-028</div>
        </span>
        <i class="separator" v-if="userInfo"></i>
        <a href="javascript:;" v-if="userInfo" @click="logout">退出</a>
      </span>
    </div>
    <div class="content-wrapper">
      <img src="../static/images/PC-LOGO.png" class="logo" @click="$router.push({ name: 'Index' })">
      <div v-show="!isLoginPage" class="search-wrapper">
        <input
          type="text"
          :placeholder="defaultSearch ? defaultSearch.keyword : '商品名称'"
          class="search-text"
          v-model="searchText"
          @keyup="searchKeywordsAutoComplete(searchText)"
          @focus="displaySearchHistory"
          @keydown.enter="searchCommodity(searchText)"
        ><button class="search-button" @click="searchCommodity(null)"  type="button" >搜索</button>

        <div v-if="searchList.length > 0" class="search-list" v-show="isCancel || scrollTop <= 40">
          <a
            href="javascript:;"
            v-for="(item, index) in searchList"
            :key="index"
            v-if="!item.isDefault"
            @click="searchCommodity(item.keyword)"
          >
            {{ item.keyword }}
          </a>
        </div>
        <ul class="search-history" >
          <li v-for="(item, index) in autoKeywords"  v-if="index < 10">
            <a href="javascript:;" @click="searchCommodity(item.context)" style="color: #333">{{ item.context }}</a>
          </li>

          <li class="search-sign" v-if="searchHistory.length > 0">最近搜索</li>
          <li v-for="(item, index) in searchHistory" :key="index" v-if="index < 10">
            <a href="javascript:;" @click="searchCommodity(item)">{{ item }}</a>
            <i class="iconfont icon-shanchu" @click.stop="removeSearchItem(item)"></i>
          </li>
        </ul>
      </div>
      <a href="javascript:;" class="cart" v-show="!isLoginPage" @click="$router.push({ name: 'Cart' })">
        <i class="iconfont icon-gouwuche"></i>
        <span>我的购物车</span>
        <i class="iconfont icon-few"></i>
        <i class="maopao">{{ cartNum }}</i>
      </a>
    </div>
    <!-- 全部分类 -->
    <div id="classify" @mouseenter="setDisplayClassify" @mouseleave="setHiddenClassify" v-show="(isCancel || scrollTop <= 40) && !isHidden">
      <div class="classify-wrapper">
        <div class="classify-button">
          <span>全部分类</span>
          <i class="iconfont icon-w" :class="displayClassify ? 'animate' : ''"></i>
        </div>
        <ul class="classify-list" v-if="displayClassify && list.length > 0">
          <li v-for="(item, index) in list" :key="index" @mouseenter="selectClassify(item)">
            <div class="category-title">
              <span class="category-name">{{ item.categoryName }}</span>
              <i class="iconfont icon-few"></i>
            </div>
            <div class="show-content">
              <div class="category-wrapper">
                <a
                  href="javascript:;"
                  v-for="(category, index) in item.secondCategoryList"
                  :key="index"
                  class="category"
                  @click="$router.push(`/list?category=${getClassThird(category.thirdCategoryList)}&parent=${category.parent}&prev=${category.categoryCode}&crumb=${encodeURIComponent(encodeURIComponent(item.categoryName+'>'+category.categoryName))}`)"
                >
                  <img :src="category.imageUrl">
                  <div class="second-category-name">{{ category.categoryName }}</div>
                </a>
              </div>
              <!-- 分类推荐商品 -->
              <div
                v-if="currentCommodity"
                class="recommend-wrapper"
                @click="$router.push({ name: 'Detail', params: { commodityCode: currentCommodity.commodityCode }})"
              >
                <img :src="currentCommodity.imageUrl">
                <div class="commodity-name">{{ currentCommodity.commodityName }}</div>
              </div>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
  <!-- 防抖动 -->
  <div v-show="!isCancel && scrollTop > 40" style="height: 182px;"></div>
</div>
</template>
<script>
import { mapState } from 'vuex'

function getOffsetLeft (node) {
  let offsetLeft = node.offsetLeft;
  let parent = node.offsetParent;
  if (parent) {
    offsetLeft += parent.offsetLeft;
    parent = parent.offsetParent
  }
  return offsetLeft
}

export default {
  name: 'Header',
  data () {
    return {
      searchList: [],
      list:JSON.parse(localStorage.localStorageMainCategory||'[]'),
      displayClassify: false,
      isLoginPage: false,
      scrollTop: 0,
      userInfo: null,
      cancelList: [ 'Login', 'Cart', 'User', 'prePay', 'Pay' ], // 取消header固定定位的页面列表
      hiddenClassifyList: [ 'Login', 'Cart', 'User', 'prePay', 'Pay' ], // 隐藏分类
      searchText: null,
      cartElement: null,
      searchHistory: [],
      searcher:null,
      autoKeywords:[],
      recommendList: null,
      currentCommodity: null,
      classifyId: null,

    }
  },
  computed: {
    ...mapState([
      'loginStatus',
      'loginNotify',
      'cartNum',
      'updateName'
    ]),
    defaultSearch: function () {
      return this.searchList.find(element => element.isDefault)
    },
    isCancel () {
      return this.cancelList.indexOf(this.$route.name) > -1
    },
    isHidden () {
      return this.hiddenClassifyList.indexOf(this.$route.name) > -1
    }
  },
  watch: {
    '$route' () {
      //输入框保留
      this.$route.query.keyword?this.searchText=decodeURIComponent(decodeURIComponent(this.$route.query.keyword)):''
      //停止订单状态查询
      vm.$emit("stopPolling");
      // 重新监听滚动事件
      window.addEventListener('scroll', this.scrollHandler)
      // 全部分类保持展开状态
      if (this.$route.name === 'Index') {
        this.displayClassify = true
      } else {
        this.displayClassify = false
      }
      // 判断是否是登录页面, 去掉额外的输入框等等, 去掉滚动
      if (this.$route.name === 'Login') {
        this.isLoginPage = true
        this.scrollTop = 0
        window.removeEventListener('scroll', this.scrollHandler)
      } else {
        this.isLoginPage = false
      }
      if (this.$route.name === 'Cart') {
        window.removeEventListener('scroll', this.scrollHandler)
      }
      // 重置
      window.scrollTo(0, 0)
      this.scrollTop = 0
      // 离开列表页时，清除搜索关键字
      if (this.$route.name !== 'List' && this.searchText) {
        this.searchText = null
      }
    },
    // 更新购物车数量
    loginStatus () {
      this.$store.dispatch('getCartNum')
    },
    // 登录成功后，更新页面数据
    loginNotify () {
      if (this.loginNotify) {
        this.getUserInfo()
        this.$store.dispatch('setLoginNotify', false)
      }
    },
    // 监听修改用户名
    updateName () {
      if (this.updateName) {
        this.getUserInfo()
        this.$store.dispatch('setUpdateName', false)
      }
    }
  },
  created () {
    this.getBasicInfo()
    this.getCustomCategory()
    this.getUserInfo()
    this.$store.dispatch('getCartNum');


    /*补全*/
    const  searchAutoKeywords=function(fn, t) {
      var timer;
      return function() {
        var that = this,
          args = arguments;
        clearTimeout(timer);
        timer = setTimeout(function() {
          fn.apply(that, args);
        }, t);
      }
    };
    this.searcher=searchAutoKeywords(this.searchAutoKeywordsDo,500);


  },
  mounted () {
    this.cartElement = document.querySelector('.icon-gouwuche')
    this.setCartPosition()
    window.onresize = this.setCartPosition
  },
  methods: {
    selectClassify (item) {
      this.recommendList = item
      this.currentCommodity = null
      let index = 0
      clearInterval(this.classifyId)
      if (this.recommendList.guessYouLikeCommodityList) {
        this.currentCommodity = this.recommendList.guessYouLikeCommodityList[index]
        this.classifyId = setInterval(() => {
          if (!this.recommendList.guessYouLikeCommodityList) return clearInterval(this.classifyId)
          let len = this.recommendList.guessYouLikeCommodityList.length
          index++
          if (len <= index) index = 0
          this.currentCommodity = this.recommendList.guessYouLikeCommodityList[index]
        }, 2000)
      } else {
        this.currentCommodity = null
      }
    },
    // 获取购物车位置
    setCartPosition () {
      let right = window.innerWidth - getOffsetLeft(this.cartElement)
      this.$store.dispatch('setCartPosition', right)
    },
    // 获取用户信息
    getUserInfo () {
      let userInfo = sessionStorage.getItem('userInfo')
      if (userInfo) this.userInfo = JSON.parse(userInfo)
    },
    // 获取类别参数
    getClassThird(thirdCategoryList){
      let classArr=thirdCategoryList||[],arr=[];
      classArr.forEach(function(x){arr.push(x['categoryCode'])})
      return arr.join(",")
    },
    //调用补全接口
     searchAutoKeywordsDo(d){
      this.axios.get(`${this.api.searchKeywordsAutoComplete}?keyword=${decodeURIComponent(encodeURIComponent(encodeURIComponent(d)))}`).then(res => {
        if (res.status === 200 && res.data.code === 200) {
          this.autoKeywords=res.data.result
        console.log(this.autoKeywords)
        }
      })
    },

    //搜索自动补全
    searchKeywordsAutoComplete(d){
      if(!d){return false}
      this.searcher(d)
    },

    // 搜索商品
    searchCommodity (keyword) {
      console.log(keyword)
      this.autoKeywords=[];
      this.searchText = keyword || this.searchText || this.defaultSearch.keyword
      let searchHistory = localStorage.getItem('searchHistory')
      searchHistory = searchHistory ? JSON.parse(searchHistory) : []
      // 判断商品是否已经在搜索列表里了，如果不在，则添加到搜索列表
      let index = searchHistory.indexOf(this.searchText)
      if (index === -1) {
        searchHistory.unshift(this.searchText)
        localStorage.setItem('searchHistory', JSON.stringify(searchHistory))
      } else {
        // 如果有
        // 如果不在第一个，那么删除以前的位置，移动到第一个来，如果在第一个不作任何操作
        if (index > 0) {
          searchHistory.splice(index, 1)
          searchHistory.unshift(this.searchText)
          localStorage.setItem('searchHistory', JSON.stringify(searchHistory))
        }
      }
      this.$router.push({ name: 'List', query: { keyword: encodeURIComponent(encodeURIComponent(this.searchText))}})
    },
    // 展示搜索历史
    displaySearchHistory () {
      this.autoKeywords=[];
      let searchHistory = localStorage.getItem('searchHistory')
      if (searchHistory) this.searchHistory = JSON.parse(searchHistory)
    },
    // 移除搜索item
    removeSearchItem (item) {
      let index = this.searchHistory.indexOf(item)
      this.searchHistory.splice(index, 1)
      localStorage.setItem('searchHistory', JSON.stringify(this.searchHistory))
    },
    // scroll事件
    scrollHandler () {
      this.scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
    },
    // 获取基础信息：自提点、行政区、热门搜索关键字
    getBasicInfo () {
      this.axios.get(this.api.getBasicInfo).then(res => {
        if (res.status === 200 && res.data.code === 200) {
          this.searchList = res.data.result.searchKeywordList
        }
      })
    },
    // 获取自定义分类
    getCustomCategory () {
      if (localStorage.localStorageMainCategory) return false
      this.axios.get(this.api.getCustomCategory).then(res => {
        if (res.status === 200 && res.data.code === 200) {
          this.list = res.data.result
          // 分类缓存
          localStorage.setItem("localStorageMainCategory",JSON.stringify(this.list))
        }
      })
    },
    // 显示全部分类
    setDisplayClassify () {
      if (this.$route.name !== 'Index') {
        this.displayClassify = true
      }
    },
    // 隐藏全部分类
    setHiddenClassify () {
      if (this.$route.name !== 'Index') {
        this.displayClassify = false
      }
    },
    // 退出
    logout () {
      this.userInfo = null
      sessionStorage.removeItem('token')
      sessionStorage.removeItem('userInfo')
      sessionStorage.removeItem('personalInfo')
      localStorage.removeItem('advertisementList')
      localStorage.removeItem('localStorageMainCategory')
      this.$store.dispatch('getLoginStatus')
      this.$router.push({ name: 'Login' })
    }
  }
}
</script>
