import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

// 定义组件地址
const Index = () => import('@/views/products/Index')
const noPage = () => import('@/views/404')
const Detail = () => import('@/views/products/Detail')
const List = () => import('@/views/products/List')
const Cart = () => import('@/views/products/Cart')
const prePay = () => import('@/views/order/prePay')
const Login = () => import('@/views/user/Login')
const User = () => import('@/views/user/User')
const Pay =()=> import('@/views/order/pay')
const helpCenter =()=> import('@/views/helpCenter/helpCenter')

const routes = [
  { path: '*', component: noPage }, // 404
  { path: '#/', redirect: { name: 'Index' }}, // 首页
  { path: '/index.html', redirect: { name: 'Index' }}, // 首页
  { path: '/index', name: 'Index', component: Index}, // 首页
  { path: '/detail/:commodityCode', name: 'Detail', component: Detail}, // 详情
  { path: '/list', name: 'List', component: List }, // 商品列表
  { path: '/cart', name: 'Cart', component: Cart, meta: { requiresAuth: true}}, // 购物车
  { path: '/prePay', name: 'prePay', component: prePay, meta: { requiresAuth: true }}, // 去支付
  { path: '/Pay/:orderNum/:payType', name: 'Pay', component: Pay, meta: { requiresAuth: true }}, // 支付
  { path: '/login', name: 'Login', component: Login }, // 登录
  { path: '/User', name: 'User', component: User, meta: { requiresAuth: true }},// 个人中心
  { path: '/helpCenter', name: 'helpCenter', component: helpCenter} // 帮助/说明中心
]

const router = new Router({
  mode: 'hash',
  routes: routes
})

router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.requiresAuth)) {
    const TOKEN = sessionStorage.getItem('token')
    if (TOKEN) {
      next()
    } else {
      next({ name: 'Login' })
    }
  } else {
    next()
  }
})

export default router



