import Vue from 'vue'
import axios from 'axios'

const TOKEN = '' ;// 临时token
window.vm= new Vue();

// 设置请求默认地址和首部信息
axios.defaults.baseURL = Host.SERVER_ADDRESS;
axios.defaults.headers.post['Content-Type'] = 'application/json;charset=UTF-8';
axios.defaults.timeout = 20000;

axios.interceptors.request.use(config => {
  config.headers['token'] = sessionStorage.getItem("token")||TOKEN;
  config.headers['device_type'] = 'okhttp';
  return config
}, err => {
  return Promise.reject(err)
});

/*拦截器*/
axios.interceptors.response.use(response => {
  switch (response.data.code*1){
    case 0:
      vm.$message.error('网络异常，请检查网络是否正常');
      break;
    case 504:
      vm.$message.error('请求后台超时，请与管理员联系!');
      break;
    case 401:
      vm.$message.error(response.data.remark);
      // location.href="/login";
      break;
    default:
      if(response.data.code>=400&&response.data.code != 500){
        vm.$message.error(response.data.remark);
      }
      break;
  }
  return response

}, error => {
  if (error.toString().indexOf("Network Error") != -1) {
    vm.$message.error("请求地址错误或未连网!");
  } else if (error.response > 500 && error.response.status) {
    vm.$message.error('服务端错误!');
  } else if (error.response && error.response.status == 408) {
    vm.$message.error('请求超时,请稍后重试');
  } else if (error.response && error.response.status == 404) {
    vm.$message.error('网络异常');
  } else if (error.response && error.response.status == 401) {
    vm.$message.error('对不起你没有权限,2秒后跳转至登录!');
    setTimeout(function(){
      sessionStorage.removeItem("token");
      //location.href="/login";
    },2000)

  } else if(error.message && error.message.indexOf("timeout") != -1) {
    vm.$message.error('请求超时!');
  }
  return Promise.reject(error)
})


/**
 * vue 通用选择器
 * **/
Vue.filter("toF",(value,dt)=>{ //默认2位小数点;
  let v=value||0,n=dt||2;
  return v.toFixed(n)
});


/*常用校验正则*/
const Regex={
  phone: /0?(13|14|15|18|17)[0-9]{9}$/,
  password:/^[a-zA-Z0-9]{8,20}$/,//密码
  phoneCode:/^[\d]{6}$/, //验证码
  realName:/^[\u4e00-\u9fa5A-Za-z]{2,20}$/,// 真实姓名
  identity:/(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)|(^\d{14}(\d|X|x)$)/,//身份证
  bankCard:/^[\d]{12,21}$/, //银行卡
  price: /^\d{1,12}(\.\d{1,2})?$/,
  numInt:/^[\d]{1,20}$/,//整数
}

/*query to param*/
const toParams=function (s){
  var s=s||'';
  var arr=(s.split("?")[1]||s||'').split("&");
  var o={};
  if(arr[0]==''){return o}
  arr.forEach(function(x){
    var ar=x.split("=");
    o[ar[0]]=ar[1]
  });
  return o
};


/*param to query*/
const toQuery=function (params){
  let str="";
  for(let j in params){
    str+=(`${j}=${params[j]}&`)
  }
  return str.substr(0,str.length-1)
};


// 定义图片尺寸
const IMAGEMIDDLEWIDTH = '?imageView2/2/w/200/h/200';
const ANDIMAGEMIDDLEWIDTH = '&imageView2/2/w/200/h/200';
const IMAGESMALLWIDTH = '?imageView2/2/w/100/h/100';




Vue.prototype.imageMiddleWidth = IMAGEMIDDLEWIDTH;
Vue.prototype.andImageMiddleWidth = ANDIMAGEMIDDLEWIDTH;
Vue.prototype.imageSmallWidth = IMAGESMALLWIDTH;

Vue.prototype.axios = axios;
Vue.prototype.$pub = {Regex,toParams,toQuery};






